import React from 'react';
import { Redirect, useRouteMatch } from 'react-router-dom';
import List from './RickAndMortyAPI/List';
import { StoreProvider } from './RickAndMortyAPI/Store';

export default () => {
  const
  base = '/Projects/Rick-And-Morty-API',
  match = useRouteMatch();

  return match.path === base ? (
    <Redirect to={base + '/page/1'} />
  ) : (
    <StoreProvider>
      <List />
    </StoreProvider>
  );
};